import React from 'react';
import './Navbar.css';
import IngCoffee from './IngCoffee';
import IngGithub from './IngGithub';
import IngLinkedin from './IngLinkedin';

class IngNavbar extends React.Component {


    



    render() {
        return (
            <div>
                <nav className="navbar fixed-top navbar-expand-lg navbar-light bg-light">
                    <a className="navbar-brand" href="#"><IngCoffee /></a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse" 
                    id="navbarSupportedContent">
                        <ul className="navbar-nav mr-auto">

                            <li><a className="nav-link" href="#">Home 
                            <span className="sr-only">(current)</span></a>   </li>
                            <li><a className="nav-link" href="#education">Background</a></li>
                            <li><a className="nav-link" href="#projects">Projects</a></li>
                            <li><a className="nav-link" href="#certs">Certifications</a></li>
                            <li><a className="nav-link" href="#contacts">Contacts</a></li>

                        </ul>
                        
                        <div className="row" id="icons-bar"><IngGithub/> <IngLinkedin/> </div>
                        
              
                    </div>



                </nav>
            </div>
        )
    }
}

export default IngNavbar;
