import React, {useState} from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import '../App.css';
import IngProjects from './IngProjects';
import IngEducation from './IngEducation';
import IngContacts from './IngContacts';
import IngCertifications from './IngCertifications';
import IngNavbar from './IngNavbar';
import PortugueseComponent from '../components/PortugueseComponent';

const IngEnglishComponent = () => {
  
  const [showPT, setShowPt] = useState(false);
  const [showEN, setShowEN] = useState(true);


  const mostraPt = () => {
    setShowPt(true);
    setShowEN(false);
  }
  


  
  return (

    <div className="App">     
      <IngNavbar/>


      {showEN ?

      <div>


      <header className="App-header" id="hello">Hello</header>
          
      <button type="button" 
      className="btn btn-light"
      onClick={mostraPt}>
       Português
        </button>
     
      </div>
      :

      null
}


{showPT ? 

  <PortugueseComponent/> 


      :

      <div className="container">
        <div className="row"> 
           <div className="col-sm">
          <IngEducation />
          </div>
        </div>     

        <div className="row">
          <div class="col-sm">
            <IngCertifications />
          </div>
        </div>
        <div className="row">
          <div class="col-sm">
            <IngProjects />
          </div>
        </div>
        <div className="row">
          <div class="col-sm">
            <IngContacts />
          </div>
        </div>
      </div>


    


}
    </div>
 
 
 
 
 
 
 
 );
}

export default IngEnglishComponent;
