import React from 'react';
import './Contacts.css';


class IngContacts extends React.Component {
    render() {
        return (
            <div>
                <div className="contacts" id="contacts">Contacts
                  <div id="contacts-text">

                        <ul>
                            <a className='linkInsideText'
                                href="https://www.linkedin.com/in/joanafpt/"
                                target="_blank" rel="noopener noreferrer">
                                LinkedIn
                            </a>
                        </ul>
                        <ul><a className='linkInsideText'
                            href="https://github.com/joanafpt"
                            target="_blank"
                            rel="noopener noreferrer">
                          Github</a></ul>
                    </div>


                </div>
            </div>
        )
    }
}
export default IngContacts;