import React, {useState} from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import '../App.css';
import Projects from './Projects';
import Education from './Education';
import Contacts from './Contacts';
import Certifications from './Certifications';
import Navbar from './Navbar';
import IngEnglishComponent from '../english_components/IngEnglishComponent';

const PortugueseComponent = () => {


  const [showPT, setShowPt] = useState(true);
  const [showUK, setShowUK] = useState(false);



  
  
  const mostraUK = () => {
    setShowPt(false);
    setShowUK(true);
  }


  return (

    <div className="App">    

     <Navbar/>


     {showPT    
 ?    
      <div>
      <header className="App-header" id="hello">Olá</header> 


      <button type="button" 
      className="btn btn-light"
      onClick={mostraUK}>
        English
        </button>
         </div>  : 
      
      null
      /*
      <div> <header className="App-header" id="hello">Hello!</header> 
      <button onClick={mostraPt}>PORTUGUIES</button></div> */
      
      
      }


 { showUK     
 ?     
 
 <IngEnglishComponent/>
 
 
 :    <div className="container">
        <div className="row"> 
           <div className="col-sm">
          <Education />
          </div>
        </div>     

        <div className="row">
          <div class="col-sm">
            <Certifications />
          </div>
        </div>
        <div className="row">
          <div class="col-sm">
            <Projects />
          </div>
        </div>
        <div className="row">
          <div class="col-sm">
            <Contacts />
          </div>
        </div>
      </div>


   }




    
 
 
 
 
</div>   
 
 
 );
}

export default PortugueseComponent;
