import React from 'react';
import './Navbar.css';
import Coffee from './Coffee';
import Github from './Github';
import Linkedin from './Linkedin';

class Navbar extends React.Component {
    render() {
        return (
            <div>
                <nav className="navbar fixed-top navbar-expand-lg navbar-light bg-light">
                    <a className="navbar-brand" href="#"><Coffee /></a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse" 
                    id="navbarSupportedContent">
                        <ul className="navbar-nav mr-auto">

                            <li><a className="nav-link" href="#">Início 
                            <span className="sr-only">(current)</span></a>   </li>
                            <li><a className="nav-link" href="#education">Background</a></li>
                            <li><a className="nav-link" href="#projects">Projetos</a></li>
                            <li><a className="nav-link" href="#certs">Certificados</a></li>
                            <li><a className="nav-link" href="#contacts">Contactos</a></li>

                        </ul>
                        <div className="row" id="icons-bar"><Github/> <Linkedin/> </div>
                        
           
                    </div>



                </nav>
            </div>
        )
    }
}

export default Navbar;
