import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub} from '@fortawesome/free-brands-svg-icons';


const element = <FontAwesomeIcon icon={faGithub} size='2x' />

 class IngGithub extends React.Component {
    render() {
        return (
            <div className='navbar-icon' id="github">
             
                <a href="https://github.com/joanafpt" 
                target="_blank" rel="noopener noreferrer">{element}</a>



            </div>
        )
    }
}
export default IngGithub;